.page {
  font-family: "Roboto", sans-serif;
  font-weight: normal;
  font-size: 16px;
  line-height: 27px;
  color: $colorBlack;

  scroll-behavior: smooth;

  &__body {
    margin: 0;

    &--with-menu {
      overflow: hidden;
    }
  }

  &__menu {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    z-index: 100;
    transform: translateX(-100%);

    transition: 0.3s ease-in;

    &:target {
      transform: translateX(0);
    }
  }

  &__button {
    width: 100%;
    height: 56px;
  }

  &__section {
    margin-bottom: 64px;

    @include onDesktop {
      margin-bottom: 104px;
    }
  }

  &__section-title {
    margin: 0 0 32px;

    font-style: normal;
    font-weight: bold;
    font-size: 36px;
    line-height: 36px;
    text-align: center;
    letter-spacing: -1.5px;
    color: $colorPureBlack;

    @include onDesktop {
      margin-bottom: 56px;

      font-size: 48px;
      line-height: 48px;
    }

    &::after {
      content: "";

      display: block;
      width: 23px;
      height: 16px;
      margin: 4px auto 0;

      background: center / contain no-repeat url(../images/crown.svg);
    }
  }
}
